// A cover block is a full size cover with a fixed background for smaller screens.

@include td-box-height-modifiers(".td-cover-block");

.td-cover-logo {
    margin-right: 0.5em;}

.td-cover-block {
    position: relative;
    color: #ffffff;
    background-color: $primary;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin: 64px 0 0 0;
    height: calc(100vh - 64px);
    display: flex;
    align-items: center;

    @include media-breakpoint-up(lg) {
        margin: 80px 0 0 0;
        height: calc(100vh - 80px);}

    &::before {
        background-color: rgba(38,38,38,.3);
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;}

    & > * {
        position: relative;
        z-index: 1;}

    &__title {
        margin: 0 auto 40px auto;
        font-weight: 700;
        line-height: 1.2;
        max-width: 350px;
        font-size: 2.5rem;

        @include media-breakpoint-up(md) {
            max-width: 700px;
            font-size: 2.75rem;}

        @include media-breakpoint-up(lg) {
            font-size: 3rem;}}

    &__description {
        margin: 0 auto 40px auto;
        max-width: 700px;}

    &__description p {
        font-weight: 300;

        @include media-breakpoint-up(md) {
            font-size: 1.15rem;}

        @include media-breakpoint-up(lg) {
            font-size: 1.25rem;}}

    &__description > *:last-child {
        margin: 0;}

    &__buttons-block {
        margin: 0 0 48px 0;}

    &__buttons-block > * {
        &:not(:last-child) {
            margin: 0 16px 0 0;}}

    &__button {
        font-size: 1rem;

        @include media-breakpoint-up(md) {
            font-size: 1.25rem;}}

    &__down-icon p {
        margin: 0;}

    &__down-icon a {
        font-size: 0.75rem;

        @include media-breakpoint-up(md) {
            font-size: 1rem;}}}
            
// 슬라이드쇼 관련 스타일을 추가합니다.

.background-slideshow {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
  
    &::before {
      content: "";
      position: absolute;
      background-color: transparent; // 배경 효과 제거 또는 조정
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
  
  .background-slide {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    opacity: 0;
    animation: slideShow 12s infinite linear; // 애니메이션 속도 조정
  }
  
  @keyframes slideShow {
    0%, 100% { opacity: 0; z-index: 1; }
    5%, 25% { opacity: 1; z-index: 2; } /* 활성 상태일 때만 z-index를 높여서 잔상이 보이지 않게 함 */
    30%, 95% { opacity: 0; z-index: 1; }
  }
  
  .background-slide:nth-child(1) { animation-delay: 0s; }
  .background-slide:nth-child(2) { animation-delay: 2s; }
  .background-slide:nth-child(3) { animation-delay: 4s; }
  .background-slide:nth-child(4) { animation-delay: 6s; }
  .background-slide:nth-child(5) { animation-delay: 8s; }
  .background-slide:nth-child(6) { animation-delay: 10s; }
